.box-shadow{
  -webkit-box-shadow: 2px 5px 12px -5px rgba(110,110,110,0.52); 
  box-shadow: 2px 3px 12px 0px rgba(110,110,110,0.14);
}

.gray-backgroundcolor{
  background-color: #f8f8f8;
}

.border-blue{
  border-bottom: 1px solid #589df8;
}

