  background-color: rgb(103, 214, 103);
}

.font-family{
  font-family: 'League Spartan', sans-serif;
}

::-webkit-scrollbar{
  height: 4px;
  background-color: transparent;
}

::-webkit-scrollbar-thumb{
  background-color: rgb(157, 203, 255);
}

.blue-color{
  color: #589df8;
}

.border-color-gray{
  border-color: #f1f1f1;
}

