  color: gray;
}

.opacity-background{
  background-color: rgba(22, 22, 22, 0.3);
}

.box-shadow-clearer{
  -webkit-box-shadow: 0px 1px 8px 9px rgba(0,0,0,0.06);
  box-shadow: 0px 1px 8px 9px rgba(0,0,0,0.06);
}

.width-almost-full{
  width: 80vw;
}

.background-gradient{
